body {
    height: 100vh;
    background-image: linear-gradient(to right, rgba(223, 223, 223, 0.2), rgba(223, 223, 223, 0.2)),url(../image/background.jpg);
    background-size: auto, cover;
    background-repeat: no-repeat, no-repeat;
    background-position: center, right bottom; }

#home img, #person-base img {
    position: fixed;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    -webkit-user-drag: none; }

/****************************/
/*     01. Section Home     */
/****************************/
#home_meme {
    left: 25%;
    top: 17.5%;
    height: 25%;
    transform: translateX(-50%) translateY(-50%);
    cursor: pointer;
    transition: height 2s ease;
    @media screen and (max-width: 1300px) {
        width: 10%;
        height: auto; } }

#home_freedom {
    left: 15%;
    top: 45%;
    height: 25%;
    transform: translateX(-50%) translateY(-50%);
    cursor: pointer;
    transition: height 2s ease;
    @media screen and (max-width: 1300px) {
        width: 10%;
        height: auto; } }

#home_cat {
    left: 20%;
    top: 72.5%;
    height: 25%;
    transform: translateX(-50%) translateY(-50%);
    cursor: pointer;
    transition: height 2s ease;
    @media screen and (max-width: 1300px) {
        width: 10%;
        height: auto; } }

#home_liver {
    right: 20%;
    top: 20%;
    height: 25%;
    transform: translateX(-50%) translateY(-50%);
    cursor: pointer;
    transition: height 2s ease;
    @media screen and (max-width: 1300px) {
        width: 10%;
        height: auto; } }

#home_workhard {
    right: 10%;
    top: 50%;
    height: 25%;
    transform: translateX(-50%) translateY(-50%);
    cursor: pointer;
    transition: height 2s ease;
    @media screen and (max-width: 1300px) {
        width: 10%;
        height: auto; } }

#home_bowl {
    top: 80%;
    left: 49%;
    width: 20%;
    transform: translateX(-50%) translateY(-40%); }

#home_magician {
    top: 80%;
    left: 50%;
    height: 70%;
    transform: translateX(-60%) translateY(-100%);
    @media screen and (max-width: 1300px) {
        width: 35%;
        height: auto; } }

#home_arrow {
    top: 65%;
    left: 54%;
    height: 20%;
    transform: translateY(-90%);
    @media screen and (max-width: 1300px) {
        width: 10%;
        height: auto; } }

#home_smoke_left {
    top: 86%;
    left: 59%;
    width: 50%;
    transform: translateX(-95%) translateY(-80%); }

#home_smoke_right {
    top: 86%;
    left: 39%;
    width: 50%;
    transform: translateX(-5%) translateY(-80%); }

/************************************/
/*     02. Section Pserson Base     */
/************************************/
.person-base-img {
    top: 83%;
    left: 43%;
    height: 85%;
    transform: translateX(-60%) translateY(-100%); }

#person-base-icon {
    color: white;
    opacity: 0.5;
    cursor: pointer; }

#person-base-bowl {
    top: 79%;
    left: 40%;
    width: 25%;
    transform: translateX(-50%) translateY(-40%);
    @media screen and (max-width: 1400px) {
        width: 30%; }
    @media screen and (max-width: 1250px) {
        width: 35%; } }

#person-base-smoke-left {
    top: 86%;
    left: 48%;
    width: 52%;
    transform: translateX(-95%) translateY(-80%); }

#person-base-smoke-right {
    top: 86%;
    left: 28%;
    width: 60%;
    transform: translateX(-5%) translateY(-80%); }

#person-base-card {
    position: fixed;
    top: 8%;
    right: 10%;
    width: 30%;
    min-height: 40%;
    background-color: rgba(223, 223, 223, 0.5);
    border-radius: 50px;
    padding: 40px;
    color: aliceblue;
    user-select: none;
    @media screen and (max-width: 1500px) {
        padding: 30px; }
    @media screen and (max-width: 1400px) {
        padding: 25px; }
    h1 {
        font-family: 'Noto Sans TC', sans-serif;
        background-color: #5E6954;
        border-radius: 17px;
        font-size: 50px;
        padding: 0 10px;
        display: inline-block;
        margin: 0;
        @media screen and (max-width: 1700px) {
            font-size: 45px; }
        @media screen and (max-width: 1500px) {
            font-size: 40px; }
        @media screen and (max-width: 1400px) {
            font-size: 35px; }
        @media screen and (max-width: 1250px) {
            margin-bottom: 10px; } }
    h2 {
        font-family: 'Noto Sans TC', sans-serif;
        background-color: #C6B974;
        border-radius: 15px;
        font-size: 30px;
        padding: 0 10px;
        display: inline-block;
        margin: 10px 5px 10px 0;
        @media screen and (max-width: 1500px) {
            font-size: 25px; } } }

#person-base-job {
    font-family: 'Give You Glory', cursive;
    position: fixed;
    top: 12%;
    right: 15%;
    font-size: 50px;
    @media screen and (max-width: 1700px) {
        top: 12%;
        right: 12%;
        font-size: 45px; }
    @media screen and (max-width: 1400px) {
        font-size: 40px; } }

#person-base-content {
    font-family: 'Noto Sans TC', sans-serif;
    background-color: #5E6954;
    white-space: pre-line;
    border-radius: 17px;
    font-size: 25px;
    width: 100%;
    line-height: 1.8;
    word-wrap: break-word;
    padding: 0 10px;
    display: inline;
    box-decoration-break: clone;
    -webkit-box-decoration-break: clone; }
